@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@400;500&family=Viaoda+Libre&family=Vibur&family=Vidaloka&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Amiri:ital,wght@0,400;0,700;1,400&display=swap');



:root {
    --viaoda-libre-font: "Viaoda Libre", cursive;
    --vidaloka-font: "Vidaloka", serif;
    --vibur-font: "Vibur", cursive;
    --dosis-font: "Dosis", sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    background: #2f2f2f;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
    position: absolute;
    perspective: 1300px;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

a {
    text-decoration: none !important;
}
