.pc-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    /* perspective: 1000px; */
}

.pc-card {
    min-height: 70%;
    width: 40vh;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0 0 50px rgba(0, 0, 0, 0.2);
    border-radius: 30px;
    padding: 0 1rem;
    transform-style: preserve-3d;
    background: #353535ef;
    /* cursor: pointer; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pc-pic {
    min-height: 35vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.pc-pic img {
    width: 20rem;
    z-index: 2 !important;
    transition: all 0.75s ease-out;
}

.pc-bg-circle {
    width: 12rem;
    height: 12rem;
    border-radius: 50%;
    background: linear-gradient(
        rgba(245, 70, 66, 0.75),
        rgba(8, 83, 156, 0.75)
    );
    position: absolute;
    z-index: 1;
    transition: all 0.5s ease-out;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.5), 0 0 50px rgba(0, 0, 0, 0.2);
}

.pc-info h1 {
    font-size: 2.5rem;
    transition: all 0.75s ease-out;
    margin: 10px 0;
    color: rgb(255, 255, 255);
    /* background: linear-gradient(
        to right,
        rgb(255, 0, 128),
        rgb(0, 132, 255),
        rgb(0, 225, 255),
        rgb(0, 255, 170),
        rgb(0, 255, 128)
    ); */
    /* -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; */
    filter: drop-shadow(0px 5px 7px rgb(10, 10, 10));
    font-family: var(--dosis-font);
}

.pc-info h3 {
    font-size: 1.3rem;
    padding-top: 2rem 0;
    color: #ccb6b6;
    font-weight: lighter;
    transition: all 0.75s ease-out;
}

.pc-btn-container {
    width: 90%;
    margin: 1.5rem;
    transition: all 0.75s ease-out;
    display: flex;
    align-items: center;
    justify-content: center;
}

.pc-btn-container a {
    text-decoration: none;
    padding: 0.5rem 1rem;
    /* background: #777373; */
    background: transparent;
    border: none;
    color: white;
    font-size: 1em;
    cursor: pointer;
    border-radius: 2px;
    border: 2px solid rgb(219, 32, 79);
    /* font-weight: bolder; */
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.5), 0 0 50px rgba(0, 0, 0, 0.2);
}

.pc-btn-container p {
    margin: 0 5px;
}

.pc-brain-wrapper {
    padding: 0.5rem 1rem;
    border: none;
    color: white;
    font-size: 1.1em;
    cursor: pointer;
    border-radius: 40px;
    background: rgb(37, 37, 37);
    /* border: 2px solid rgb(219, 32, 79); */
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.5), 0 0 50px rgba(0, 0, 0, 0.2);
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 5px 5px;
}

.pc-brain {
    width: 3em;
    filter: drop-shadow(0px 5px 7px black);
    margin: 0 10px;
}
