/* Overrides */
.awssld {
    --control-button-width: 25% !important;
}

.awssld__wrapper {
    /* margin: auto; */
}

.awssld__next {
    /* --control-button-width: 5% !important;
    right: 105px !important; */
    z-index: 1 !important;
}
.awssld__prev {
    /* --control-button-width: 5% !important;
    left: 105px !important; */
    z-index: 1 !important;
}

/* -------------- */
.projects-container {
    width: calc(100%);
    height: 100%;
    margin: auto;
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
}

.projects-slide {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #2b2b2b;
    margin: 300px 0;
}

.projects-slide img {
    width: 70%;
    height: 90%;
    border-radius: 20px;
    box-shadow: 5px 5px 15px 5px black;
}

.project-btn {
    display: flex;
    width: 100px;
    height: 100px;
    padding: 20px 20px;
    background: linear-gradient(
        145deg,
        rgb(0, 0, 0),
        rgb(37, 34, 34),
        rgb(5, 5, 5)
    );
    position: fixed;
    border-radius: 100px;
    text-decoration: none;
    bottom: 10px;
    filter: drop-shadow(-3px 10px 10px black);
    justify-content: center;
    align-items: center;
    transition: 0.5s;
    border: none;
    outline: none;
}

.project-btn:hover {
    background: linear-gradient(
        -145deg,
        rgb(0, 0, 0),
        rgb(37, 34, 34),
        rgb(5, 5, 5)
    );

    cursor: pointer;
    transition: 0.5s;
}

.project-btn h1 {
    width: 60px;
    height: 60px;
    font-size: 1.3em;
    font-weight: 700;
    color: white;
    text-align: center;
    align-items: center;
    justify-content: center;
    /* animation: projectBtnAnim 1s infinite linear; */
}
/*  ------- go project btn ---------- */
.go-project-btn {
    display: flex;

    padding: 13px 20px;
    background: linear-gradient(
        145deg,
        rgb(0, 0, 0),
        rgb(37, 34, 34),
        rgb(5, 5, 5)
    );
    border-radius: 100px;
    text-decoration: none;
    /* filter: drop-shadow(-3px 10px 10px black); */
    justify-content: center;
    align-items: center;
    transition: 0.5s;
    outline: none;
    border: none;
    cursor: pointer;
}
.go-project-btn h1 {
    font-size: 1em;
    font-weight: 700;
    color: white;
    text-align: center;
    align-items: center;
    justify-content: center;
    /* animation: projectBtnAnim 1s infinite linear; */
}

.go-project-btn:hover {
    background: linear-gradient(
        -145deg,
        rgb(0, 0, 0),
        rgba(37, 34, 34, 0.877),
        rgb(5, 5, 5)
    );

    filter: drop-shadow(2px 5px 4px black);
    transform: translateY(-4px);
    transition: 0.9s;
}

.go-project-close-btn {
    outline: none;
    border: none;
    /* font-size: 1.3em; */
    color: white;
    background: linear-gradient(-45deg, black, rgb(68, 63, 63), black);
    padding: 3px 4px;
    border-radius: 150px;
    cursor: pointer;
    margin: 0 10px;
    transition: 0.5s;
}

.go-project-close-btn:hover {
    background: linear-gradient(45deg, black, rgb(68, 63, 63), black);
    filter: drop-shadow(2px 5px 4px black);
    transform: translateY(-4px);
    transition: 0.9s;
}

@keyframes projectBtnAnim {
    0% {
        background: linear-gradient(
            to right,
            rgb(255, 254, 255),
            rgb(131, 134, 134),
            rgb(167, 168, 168),
            rgba(233, 233, 233, 0.459)
        );
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    100% {
        background: linear-gradient(
            to left,
            rgb(255, 254, 255),
            rgb(131, 134, 134),
            rgb(167, 168, 168),
            rgba(233, 233, 233, 0.459)
        );
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 1.6em;
    }
}

@keyframes a {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(360deg);
    }
}

@media (max-width: 960px) {
    .awssld {
        --control-button-width: 15% !important;
    }
    .projects-container {
        width: 100%;
        height: calc(100% - 20vh);
        margin-top: 150px;
        right: unset;
    }

    .project-btn {
        bottom: 10px;
        left: 130px;
    }
}
@media (max-width: 630px) {
    .projects-container {
        height: 50vh;
    }
}
