.navbar-container {
    width: 100px;
    height: 100%;
    background: #2b2b2b;
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    box-shadow: 5px -2px 15px -2px black;
    z-index: 9;
}

.nav-links {
    list-style: none;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
}

.nav-links li {
    height: 110px;
    padding: 25px 25px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-links .nav-link {
    text-decoration: none;
    color: #fff;
    font-size: 2.8em;
}

.nav-logo {
    color: white;
    font-size: 1.8em;
}

.nav-logo a {
    text-decoration: none;
}

.follow h1 {
    color: rgb(0, 153, 255);
    font-size: 1.5em;
    cursor: pointer;
    transition: 0.5s;
    position: absolute;
    display: inline;
}
.follow:hover h1 {
    font-size: 1.55em;
}
.follow {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.1s;
}

.menu-icon {
    display: none;
}

.nav-active {
    width: 100%;
    height: 100% !important;
    flex-direction: column !important;
    position: fixed;
}

.ul-active {
    flex-direction: column;
    visibility: visible;
    pointer-events: visible;
}

.soacial-icons-wrapper {
    display: flex;
    flex-direction: column;
    list-style: none;
    width: 100%;
}

.social-icons {
    display: flex;
    padding: 5px 20px;
    transition: 0.2s;
}
.social-icons:hover {
    background: rgba(161, 161, 159, 0.438);
}
.social-icons:hover h5 {
    font-size: 1.05em;
    background: linear-gradient(
        to right,
        rgb(51, 190, 179),
        rgb(17, 124, 185),
        rgb(197, 24, 139)
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    /* transition: 3s; */
}
.social-icons a {
    text-decoration: none;
    display: flex;
}

.social-icons h5 {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    font-size: 1em;
    font-weight: 700;
    letter-spacing: 2px;
    color: black;
}

.social-dialog-close-btn {
    outline: none;
    border: none;
    /* font-size: 1.3em; */
    color: white;
    background: linear-gradient(-45deg, black, rgb(68, 63, 63), black);
    padding: 3px 4px;
    border-radius: 150px;
    cursor: pointer;
    margin-right: 10px;
    transition: 0.5s;
    /* filter: drop-shadow(2px 5px 4px black); */
}

.social-dialog-close-btn:hover {
    background: linear-gradient(45deg, black, rgb(68, 63, 63), black);
    filter: drop-shadow(2px 5px 4px black);
    transform: translateY(-4px);
    transition: 0.7s;
}

.hide-item {
    visibility: hidden;
    pointer-events: none;
    position: absolute;
    opacity: 0;
}

.logo-name {
    font-weight: 400;
    font-size: 0.6em;
    color: rgb(223, 223, 223);
    transition: 0.3s;
}

.logo-name:hover {
    color: rgb(197, 24, 105);
}
.active-icon {
    color: rgb(197, 24, 105);
}
@keyframes social-anim {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media (max-width: 960px) {
    .navbar-container {
        flex-direction: row;
        width: 100%;
        height: 100px;
        position: absolute;
        top: 0;
    }

    .nav-links {
        flex-direction: row;
    }
    .follow {
        font-size: 0.7em;
    }
}

@media (max-width: 630px) {
    .nav-active {
        position: fixed !important;
    }
    .nav-links {
        flex-direction: column;
        visibility: hidden;
        pointer-events: none;
    }
    .nav-links li {
        margin: 0;
    width: 0%;

    }
    .nav-links.ul-active {
        flex-direction: column;
        visibility: visible;
        pointer-events: visible;
        width: 100%;
    }

    .nav-links.nav-active li:hover {
        background: #494848;
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 8px;
        right: 0;
        transform: translate(-100%, 60%);
        cursor: pointer;
        color: #fff;
        letter-spacing: 0.3em;
        font-size: 2em;
    }

    .nav-logo {
        padding: 25px 0 20px 0;
    }
}
