    width: calc(100% - 100px);
    height: 100%;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.left-wrapper,
.right-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
}

.left-wrapper {
    flex-direction: column;
    padding-left: 14% !important;
    padding-right: 13% !important;
}

.left-wrapper .home-intro {
    font-size: 4.5em;
    font-weight: 100;
    padding: 20px 0;
    font-family: var(--viaoda-libre-font);
