.switch {
    
    position: relative;
    display: inline-block;
    margin: 0 5px;
    cursor: pointer;
}

.switch > span {
    position: absolute;
    top: 14px;
    pointer-events: none;
    font-family: "Helvetica", Arial, sans-serif;
    font-weight: bold;
    font-size: 12px;
    text-transform: uppercase;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    width: 50%;
    text-align: center;
}

input.check-toggle-round-flat:checked ~ .off {
    color: #f32552;
}

input.check-toggle-round-flat:checked ~ .on {
    color: #fff;
}

.switch > span.on {
    left: 1px;
    padding-left: -5px;
    color: #f32552;
    top: 11px;
    font-size: 0.9rem;
}

.switch > span.off {
    right: 4px;
    padding-right: 2px;
    color: #fff;
    top: 11px;
    font-size: 0.9rem;
}

.check-toggle {

    position: absolute;
    margin-left: -9999px;
    visibility: hidden;
}
.check-toggle + label {
    
    display: block;
    position: relative;
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

input.check-toggle-round-flat + label {
    padding: 2px;
    width: 85px;
    height: 35px;
    background-color: #ffffff;
    border-radius: 60px;
}
input.check-toggle-round-flat + label:before,
input.check-toggle-round-flat + label:after {
    display: block;
    position: absolute;
    content: "";
}

input.check-toggle-round-flat + label:before {
    top: 2px;
    left: 2px;
    bottom: 2px;
    right: 2px;
    background-color: #000000;
    border-radius: 60px;
}
input.check-toggle-round-flat + label:after {
    top: 4px;
    left: 4px;
    bottom: 4px;
    width: 40px;
    background-color: #fff;
    border-radius: 52px;
    transition: margin 0.2s;
}

input.check-toggle-round-flat:checked + label:after {
    margin-left: 36px;
}

