:root {
    --text-color-neon: rgb(231, 58, 116);
    --border-color: #3da7f8;
    --animation-time: 3s;
    --text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px var(--text-color-neon), 0 0 10px var(--text-color-neon),
        0 0 10px var(--text-color-neon), 0 0 10px var(--text-color-neon),
        0 0 10px var(--text-color-neon), 0 0 20px var(--text-color-neon),
        0 0 30px var(--text-color-neon), 0 0 40px var(--text-color-neon),
        0 0 55px var(--text-color-neon), 0 0 75px var(--text-color-neon);
}

.neon {
    background-color: transparent;
    display: inline-block;
    transform: rotate(-6deg);
    /* animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder calc(var(--animation-time) * 16) infinite step-end 8s; */
 
   
    font-family: "Dancing Script", cursive;
    font-weight: bolder;
    
}

p {
 
    color: #fff;
}

span {
   font-size: 1.5em;
}

p span {
    opacity: 0.33;
    animation: turnOn forwards step-end;
}

p span:nth-child(1) {
    animation-delay: 0.3s;
}

p span:nth-child(2) {
    animation-delay: 0.2s;
}

p span:nth-child(3) {
    animation-delay: 0.1s;
}

p span:nth-child(4) {
    animation: turnOn 0.4s forwards step-end,
        flicker 3s infinite step-end 2s;
    animation: turnOn 0.4s forwards step-end,
        flicker var(--animation-time) infinite step-end 2s;
}

@keyframes turnOn {
    100% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
        opacity: 1;
    }
}

@keyframes flicker {
    0% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
    }

    30% {
        text-shadow: none;
    }
    33% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
    }

    35% {
        text-shadow: none;
    }
    37% {
        text-shadow: 0 0 5px rgb(167, 2, 71), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 10px rgb(231, 58, 116),
        0 0 10px rgb(231, 58, 116), 0 0 20px rgb(231, 58, 116),
        0 0 30px rgb(231, 58, 116), 0 0 40px rgb(231, 58, 116),
        0 0 55px rgb(231, 58, 116), 0 0 75px rgb(231, 58, 116);
        text-shadow: var(--text-shadow);
    }

    99% {
        text-shadow: none;
    }
}

.contact-container {
    background: var(--bg-dark);
    height: 90vh;
    width: calc(100% - 100px);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    position: relative;
}

.grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.border-wrapper {
    border: 5px solid white;
    min-height: 500px;
    height: 90%;
    width: 100%;
    -webkit-filter: drop-shadow(18px 15px 2px rgba(0, 0, 0, 0.295));
            filter: drop-shadow(18px 15px 2px rgba(0, 0, 0, 0.295));
    display: flex;
    border-radius: 20px;
    background: linear-gradient(180deg, #191927 0%, #1e1b36 100%);
    align-items: center;
    flex-direction: column;
    justify-content: center;

    padding: 80px;
}

.form-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.contact-send-btn {
    width: 25%;
    min-width: 145px;
    padding: 1.2em;
    background: #000000;
    border-radius: 100px;
    font-size: 1em;
    -webkit-filter: drop-shadow(5px 9px 12px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(5px 9px 12px rgba(0, 0, 0, 0.25));
    color: #fff;
    margin-top: 5%;
    cursor: pointer;
    transition: 0.5s;
}

.contact-send-btn:hover {
    transform: translateY(-8px);
    -webkit-filter: drop-shadow(5px 9px 15px rgba(0, 0, 0, 0.603));
            filter: drop-shadow(5px 9px 15px rgba(0, 0, 0, 0.603));
    transition: 1s;
}

.contact-send-btn:focus {
    outline: none;
}

.form-title {
    color: #fff;
    padding: 10px 0;
    text-transform: capitalize;
    font-family: var(--vibur-font);
    font-weight: normal;
    letter-spacing: 0.02em;
    -webkit-filter: drop-shadow(5px 9px 5px rgba(0, 0, 0, 0.603));
            filter: drop-shadow(5px 9px 5px rgba(0, 0, 0, 0.603));
    background: linear-gradient(
        90deg,
        #1bd1e9 0%,
        rgba(151, 21, 211, 0.952) 50.52%,
        #da0d62 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
@media (max-width: 960px) {
    .contact-container {
        width: 100%;

        margin: 150px 0;
    }
}

@media (max-width: 750px) {
    .border-wrapper {
        padding: 30px;
    }
}

.skills-container {
    width: 100%;
    height: 105vh;
    position: relative;
    margin-left: auto;
    background: linear-gradient(45deg, #232526, #414345 100%);
}

.skills-col-wrapper {
    width: 100%;
    height: 100%;
    font-size: 8em;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding-top: 100px !important;
}

.skills-col-wrapper > * {
    cursor: pointer;
    -webkit-filter: drop-shadow(5px 9px 3px rgba(0, 0, 0, 0.603));
            filter: drop-shadow(5px 9px 3px rgba(0, 0, 0, 0.603));
    transition: 0.5s;
    padding: 10px 0;
    font-size: 0.9em;
}

.skills-col-wrapper > *:hover {
    font-size: 1em;
    transition: 0.5s;
}

.skill-present-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-top: 150px !important;
}

@media (max-width: 960px) {
    .skills-container {
        width: 100%;
        height: 120vh;
        margin-top: 100px;
    }
    .skills-col-wrapper {
        flex-direction: row;
        flex-wrap: wrap;
    }
}

@media (max-width: 430px) {
    .skills-col-wrapper {
        font-size: 6em;
    }
}

.skill-card {
    width: 400px;
    height: 600px;
    background: linear-gradient(
        202.97deg,
        #fffaf5 3.01%,
        rgba(255, 249, 226, 0.68) 98.33%
    );
    box-shadow: 18px 23px 22px rgba(0, 0, 0, 0.25);
    border-radius: 34px;
    margin: 0px 10px;
    position: relative;
}
.card-wrapper {
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.skill-icon {
    font-size: 7.9em;
    -webkit-filter: drop-shadow(5px 9px 3px rgba(0, 0, 0, 0.603));
