    position: absolute;
    top: 15px;
}

.skill-title {
    font-size: 1em;
    font-weight: 100;
    color: black;
    margin-bottom: 20px;
    -webkit-filter: drop-shadow(5px 5px 3px rgba(0, 0, 0, 0.603));
            filter: drop-shadow(5px 5px 3px rgba(0, 0, 0, 0.603));
}

.skill-desc {
    margin-bottom: 20px;
    padding: 0 15px;
}
.skill-desc p {
    color: black;
    font-size: 1.3em;
    line-height: 1.3em;
    letter-spacing: 0.01em;
}

.skill-libs {
    width: 100%;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 25px;
}
.skill-lib {
    background: #8deeff;
    padding: 10px 10px;
    border-radius: 20px;
    font-size: 1em;
    font-weight: 700;
    -webkit-filter: drop-shadow(3px 8px 5px rgba(0, 0, 0, 0.445));
            filter: drop-shadow(3px 8px 5px rgba(0, 0, 0, 0.445));
    margin: 10px;
}

.skill-lib:hover {
    transform: translateY(-2px);
    cursor: pointer;
}


