.skills-container {
    width: 100%;
    height: 105vh;
    position: relative;
    margin-left: auto;
    background: linear-gradient(45deg, #232526, #414345 100%);
}

.skills-col-wrapper {
    width: 100%;
    height: 100%;
    font-size: 8em;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding-top: 100px !important;
}

.skills-col-wrapper > * {
    cursor: pointer;
    filter: drop-shadow(5px 9px 3px rgba(0, 0, 0, 0.603));
    transition: 0.5s;
    padding: 10px 0;
    font-size: 0.9em;
}

.skills-col-wrapper > *:hover {
    font-size: 1em;
    transition: 0.5s;
}

.skill-present-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-top: 150px !important;
}

@media (max-width: 960px) {
    .skills-container {
        width: 100%;
        height: 120vh;
        margin-top: 100px;
    }
    .skills-col-wrapper {
        flex-direction: row;
        flex-wrap: wrap;
    }
}

@media (max-width: 430px) {
    .skills-col-wrapper {
        font-size: 6em;
    }
}
