.skill-card {
    width: 400px;
    height: 600px;
    background: linear-gradient(
        202.97deg,
        #fffaf5 3.01%,
        rgba(255, 249, 226, 0.68) 98.33%
    );
    box-shadow: 18px 23px 22px rgba(0, 0, 0, 0.25);
    border-radius: 34px;
    margin: 0px 10px;
    position: relative;
}
.card-wrapper {
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.skill-icon {
    font-size: 7.9em;
    filter: drop-shadow(5px 9px 3px rgba(0, 0, 0, 0.603));
    position: absolute;
    top: 15px;
}

.skill-title {
    font-size: 1em;
    font-weight: 100;
    color: black;
    margin-bottom: 20px;
    filter: drop-shadow(5px 5px 3px rgba(0, 0, 0, 0.603));
}

.skill-desc {
    margin-bottom: 20px;
    padding: 0 15px;
}
.skill-desc p {
    color: black;
    font-size: 1.3em;
    line-height: 1.3em;
    letter-spacing: 0.01em;
}

.skill-libs {
    width: 100%;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 25px;
}
.skill-lib {
    background: #8deeff;
    padding: 10px 10px;
    border-radius: 20px;
    font-size: 1em;
    font-weight: 700;
    filter: drop-shadow(3px 8px 5px rgba(0, 0, 0, 0.445));
    margin: 10px;
}

.skill-lib:hover {
    transform: translateY(-2px);
    cursor: pointer;
}
