.contact-container {
    background: var(--bg-dark);
    height: 90vh;
    width: calc(100% - 100px);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    position: relative;
}

.grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.border-wrapper {
    border: 5px solid white;
    min-height: 500px;
    height: 90%;
    width: 100%;
    filter: drop-shadow(18px 15px 2px rgba(0, 0, 0, 0.295));
    display: flex;
    border-radius: 20px;
    background: linear-gradient(180deg, #191927 0%, #1e1b36 100%);
    align-items: center;
    flex-direction: column;
    justify-content: center;

    padding: 80px;
}

.form-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.contact-send-btn {
    width: 25%;
    min-width: 145px;
    padding: 1.2em;
    background: #000000;
    border-radius: 100px;
    font-size: 1em;
    filter: drop-shadow(5px 9px 12px rgba(0, 0, 0, 0.25));
    color: #fff;
    margin-top: 5%;
    cursor: pointer;
    transition: 0.5s;
}

.contact-send-btn:hover {
    transform: translateY(-8px);
    filter: drop-shadow(5px 9px 15px rgba(0, 0, 0, 0.603));
    transition: 1s;
}

.contact-send-btn:focus {
    outline: none;
}

.form-title {
    color: #fff;
    padding: 10px 0;
    text-transform: capitalize;
    font-family: var(--vibur-font);
    font-weight: normal;
    letter-spacing: 0.02em;
    filter: drop-shadow(5px 9px 5px rgba(0, 0, 0, 0.603));
    background: linear-gradient(
        90deg,
        #1bd1e9 0%,
        rgba(151, 21, 211, 0.952) 50.52%,
        #da0d62 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
@media (max-width: 960px) {
    .contact-container {
        width: 100%;

        margin: 150px 0;
    }
}

@media (max-width: 750px) {
    .border-wrapper {
        padding: 30px;
    }
}
