.left-wrapper .job-title {
    font-family: var(--vibur-font);
    font-weight: 100;
    letter-spacing: 0.2em;
    font-size: 1.3em;
    color: rgb(182, 173, 173);
    width: 100%;
}

.home-buttons-container {
    width: 100%;
    padding: 10% 0;
}

.home-contact-btn {
    margin: 25px 0 !important;
    margin-right: 20px !important;
}

.home-contact-btn a {
    text-decoration: none;
    color: inherit;
    font-family: var(--vidaloka-font) !important;
    letter-spacing: 0.04em;
    text-transform: capitalize;
}

.circles {
    position: relative;
    display: flex;
}

.circle-img {
    transform: rotate(180deg);
    animation: rotate-circle 20s infinite linear;
    width: 400px;
}
.small-circle-img {
    width: 300px;
    position: absolute;
    top: 200px;
    animation: rotate-small-circle 20s infinite linear;
}

@keyframes rotate-circle {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
@keyframes rotate-small-circle {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-360deg);
    }
}

@media (max-width: 960px) {
    .home-grid-container {
        width: 100% !important;
    }
}

@media (max-width: 750px) {
    .home-grid-container {
        margin-top: 150px !important;
    }
}
@media (max-width: 630px) {
    .left-wrapper .home-intro {
        font-size: 2.5em;
    }
    .circles {
        position: absolute;
        top: 90%;
    }
    .home-contact-btn {
        font-size: 0.8em !important;
    }

    .circle-img {
        width: 200px;
    }
    .small-circle-img {
        width: 160px;
        top: 150px;
    }
}
